        p Para lograr este objetivo, es esencial contar con una persona responsable, creativa y participativa que sepa ejercer un liderazgo efectivo. La gerencia debe formular la misin y la visin de la empresa, estableciendo metas claras y alcanzables, y asegurando una ventaja competitiva sostenida. Esta forma de gestin est estrechamente vinculada a la estrategia, entendida como el plan mediante el cual la empresa reafirma su posicin en el mercado y alcanza sus objetivos. 
