    .titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 3.1  Seguimiento, revisin, registro e informe
    
    
    .row.mb-5(data-aos='fade')
      .col-lg-5.pe-lg-0.d-none.d-lg-block
