        .br-l.bg1.p-4.h-100.d-flex.align-items-center.mont1
          p El director es el elemento en quien se basa la continuidad de la empresa, ste como centro, es el encargado de hacer productivos los recursos, es el responsable de lograr, de un modo organizado, el progreso de la iniciativa empresarial. La direccin de la empresa debe hacerse mediante objetivos, y ser capaz de tomar decisiones estratgicas, e implementarlas junto con un equipo de trabajo integrado y organizado por l mismo. Para lograr lo propuesto, el directivo debe fundamentarse en la formacin personal, teniendo una total comprensin de su empresa tanto en su mercado, como en el entorno externo que lo rodea. <br><br>Es importante que el director d un manejo adecuado a las presiones a las que se pueda enfrentar, personal o profesionalmente, a la hora de ejercer su accin de liderazgo dentro de la empresa, ya que, si no se gestionan adecuadamente, podran afectar negativamente su capacidad para liderar de manera efectiva.
    
    .row.align-items-center.subt.mb-4(data-aos="fade-left")
