        h3.mb-0 Conocimiento y alcance de la meta

    p El xito futuro de una empresa depende de un proceso administrativo eficaz, y este comienza con una adecuada planeacin. La planeacin debe estar orientada hacia el entorno, la misin de la empresa, y debe fomentar la innovacin y la creatividad. Para que sea realmente estratgica, la planeacin debe anticiparse a los cambios futuros y no simplemente proyectar el pasado.
    p Para guiar este proceso, los directivos deben plantearse y responder a las siguientes preguntas:
    .row.justify-content-center.align-items-center.mb-5(data-aos="flip-up")
