          |Qu valora nuestro cliente?
          li
          .lista-ol--cuadro__vineta
            span 3
          |Qu productos o servicios necesitan nuestros clientes?
          li
          .lista-ol--cuadro__vineta
            span 4
          |Qu ofrece nuestra competencia y qu ofrecemos nosotros?
          li
          .lista-ol--cuadro__vineta
            span 5
          |Qu tecnologas debemos emplear para desarrollar nuestros productos o servicios?
          li
          .lista-ol--cuadro__vineta
            span 6
          |Pensando en el futuro, cul es nuestro negocio actual?, cul ser? y cul debera ser?
 
         
      
      .col-lg-5.mb-3.col-9
        img(src='@/assets/curso/temas/t3/img08.png', alt='Texto que describa la imagen')
      p La planeacin estratgica es un proceso gerencial que busca alinear los objetivos y recursos de la empresa con las oportunidades cambiantes del mercado. Para llevar a cabo esta planeacin de manera efectiva, es necesario realizar un anlisis externo que permita maximizar las oportunidades y minimizar las amenazas del entorno, as como un anlisis interno para identificar las fortalezas y mitigar las debilidades. Con estos anlisis, la empresa puede formular una postura estratgica que le permita aprovechar al mximo sus recursos y asegurar su competitividad en el mercado.
