      h2 3.2  La gerencia del mercadeo
    
    p En el entorno competitivo actual, las empresas deben centrarse en el mercado y en el cliente, ms que en las ventas o el producto. Este enfoque requiere cambios significativos en:
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="flip-up")
