        img.mb-3(src='@/assets/curso/temas/t3/img09.svg', alt='Texto que describa la imagen')
    
        p Para que los directores de mercadeo alcancen sus objetivos, es necesario contar con un buen plan y una estructura organizacional adecuada. Mientras que en empresas pequeas una sola persona puede realizar tareas como investigacin, ventas, publicidad y servicios, en empresas grandes se requiere un equipo especializado, por lo tanto, es necesaria la implementacin de actividades clave al interior del departamento de mercadeo, entre ellas estn las siguientes:
