          li
          .lista-ol--cuadro__vineta
            span 2
          |#[b Colaboracin con otras reas:] integrar las decisiones de mercadeo con las de otros departamentos.

          li
          .lista-ol--cuadro__vineta
            span 3
          |#[b Seleccin y formacin:] capacitar y dirigir al personal del rea.
    
    .row.align-items-center.subt.mb-4(data-aos="fade-left")
