        h3.mb-0 Importancia en la gerencia de mercadeo

    p La gerencia de mercadeo es responsable de la gestin de la demanda, ya que sus decisiones influyen directamente en el nivel, momento y composicin de esta. Algunos ejemplos de cmo acta el departamento ante diversas situaciones incluyen:
    
    .row.justify-content-center.align-items-center.mb-5
