                  p.mb-3 Como en el caso de vacunas o clnicas dentales, donde el mercadeo debe identificar las causas de rechazo y trabajar en rediseo, precios o promocin para cambiar las actitudes del consumidor.
              
              .row.justify-content-center.align-items-center.p-4
