                  p.mb-3 Enfrentar variaciones estacionales o picos de demanda mediante precios flexibles, promocin u otros incentivos.
                 
    .row.align-items-center.subt.mb-4(data-aos="fade-left")
      .col-2.col-md-auto
        img(src='@/assets/curso/temas/t3/subt.svg', alt='')
      .col-10.col-md-auto.ps-0
        h3.mb-0 El departamento de mercadeo y su interaccin dentro de la empresa
 
 
    .row.mb-2(data-aos='fade')
      .col-lg-5.pe-lg-0.d-none.d-lg-block
        img(src='@/assets/curso/temas/t3/img14.png', alt='')
