        .br-l.bg1.p-4.h-100.d-flex.align-items-center.mont1
          p Todos los niveles que integran el negocio deberan interactuar armoniosamente con el fin de conseguir los objetivos generales de la empresa. Muchas veces, en la prctica, las relaciones interdepartamentales se caracterizan por presentar grandes rivalidades; estos conflictos, en ocasiones, surgen como consecuencia de las diferencias de opinin sobre lo que es mejor para los intereses de la empresa. <br><br>En una empresa, cada departamento tiene su impacto potencial en la satisfaccin de requerimientos por parte del consumidor, a travs de sus actividades y decisiones. Bajo el concepto de mercadeo, todos los departamentos necesitan pensar en el cliente y trabajar conjuntamente para satisfacer sus necesidades y expectativas. 
      .py-2
      p El director de mercadeo tiene dos responsabilidades principales:
      ol.lista-ol--cuadro
          li
          .lista-ol--cuadro__vineta
            span 1
          |Coordinar las actividades de mercadeo interno de la empresa. 
          li
          .lista-ol--cuadro__vineta
            span 2
          |Coordinar con las finanzas la operatividad y funcionalidad de la empresa, siempre desde el inters de los clientes.
      p A menudo, existe incertidumbre sobre la autoridad que debe ejercer el departamento de mercadeo sobre los dems, siendo preferible un enfoque persuasivo en lugar de autoritario, ya que los departamentos suelen centrarse en sus propias tareas y prioridades.
    
    .row.align-items-center.subt.mb-4(data-aos="fade-left")
      .col-2.col-md-auto
        img(src='@/assets/curso/temas/t3/subt.svg', alt='')
