        h3.mb-0 La mejor publicidad es la que hacen los clientes satisfechos
  
      
    .row.justify-content-center.align-items-center.mb-5(data-aos='fade')
      .col-lg-12
        p La publicidad es una herramienta de promocin de las ideas, bienes o servicios, que debe tener una importante participacin dentro de los departamentos de mercadeo de las empresas.
        p En el desarrollo de un programa de publicidad, los directores de mercadeo deben comenzar siempre por la identificacin del pblico objetivo y de sus motivos de compra, luego establecer los objetivos de mercado, el presupuesto, el mensaje que se quiere transmitir, los medios a utilizar y los resultados a medir.
        p Los diversos estudios realizados a la accin de directores de empresas de productos de gran consumo, acerca de cmo establecen su presupuesto de ventas y cmo lo asignan a publicidad, respecto de la promocin de ventas y promocin comercial, han concluido en los siguientes principios:

    .row.justify-content-center.align-items-center.mb-5(data-aos='fade')
