                .col.md-10.mb-4-md-0.mb-4
                  p.mb-3 Se forma un equipo de marca, tras un anlisis extensivo de la situacin el equipo establece los objetivos de mercadeo y una amplia estrategia.
              
              .row.justify-content-center.align-items-center.p-4
                .col-md-2.mb-4.mb-md-0.col-4
                  img(src='@/assets/curso/temas/t3/img25.png', alt='Texto que describa la imagen')
                .col.md-10.mb-4-md-0.mb-4
                  p.mb-3 Despus de estimar las ventas de marca y sus beneficios basados en la amplia estrategia, el equipo desarrolla una asignacin inicial del presupuesto para publicidad, promocin al consumidor y promocin comercial.
              
              .row.justify-content-center.align-items-center.p-4
                .col-md-2.mb-4.mb-md-0.col-4
                  img(src='@/assets/curso/temas/t3/img26.png', alt='Texto que describa la imagen')
                .col.md-10.mb-4-md-0.mb-4
                  p.mb-3 El equipo se apoya en la asignacin del presupuesto del ao anterior, lo que puede tener sentido si el entorno es estable, solicitando un nuevo conjunto de puntos de partida.
                     
              .row.justify-content-center.align-items-center.p-4
                .col-md-2.mb-4.mb-md-0.col-4
                  img(src='@/assets/curso/temas/t3/img27.png', alt='Texto que describa la imagen')
                .col.md-10.mb-4-md-0.mb-4
                  p.mb-3 Se presenta el plan de marca a la direccin general, que puede solicitar cambios. El plan revisado se implementa luego.
              
              .row.justify-content-center.align-items-center.p-4
                .col-md-2.mb-4.mb-md-0.col-4
                  img(src='@/assets/curso/temas/t3/img28.png', alt='Texto que describa la imagen')
                .col.md-10.mb-4-md-0.mb-4
