                  p.mb-3 Hacia el final del perodo, con frecuencia, se reemplaza por mayor promocin de ventas si la marca no est consiguiendo los objetivos de beneficio.
    

    
    
    
    .row.justify-content-center.align-items-center.mb-5(data-aos='fade')
