      h2 3.3  La gerencia de los recursos humanos

    p La gestin del recurso humano es clave para la administracin y orientacin del trabajo de los empleados en una organizacin. Desde esta perspectiva, la Gestin Humana surge como un sistema moderno que promueve el aprendizaje y el desarrollo de competencias personales a travs de la coordinacin del trabajo y la implementacin de estrategias que mejoran el conocimiento.
    p.mb-4 Entre las principales funciones de la Gestin Humana se destacan:
    
    .row.justify-content-center.align-items-center.mb-5(data-aos='fade')
